import Vue from 'vue';
import { Toast } from 'vant';
// 全局注册
Vue.use(Toast);

class ValidForm{
  // 验证表单
  valid(o){
    for(let key in o){
      if(!o[key].reg.test(o[key].value)){
        // 失败提示
        Toast(o[key].errorMsg);
        // 验证失败
        return false;
      }
    }
    // 验证成功
    return true;
  }
}

export const validForm = new ValidForm();