<template>
  <div class="forgot">
    <van-nav-bar
      left-text="返回"
      title="找回密码"
      left-arrow
      @click-left="back"
    />

    <div class="content">
      <div class="form-box">
        <van-form>
          <div class="title">通过邮箱找回</div>
          <van-field
            v-model="phone"
            label="手机号"
            placeholder="手机号"
          />
          <van-field
            v-model="email"
            label="邮箱"
            placeholder="邮箱"
          />
          <van-field
            v-model="code"
            center
            clearable
            label="验证码"
            placeholder="请输入验证码"
          >
            <template #button>
              <van-button size="small" color="#4a6fe7" :disabled='isSend' @click="getEmailValidCode">{{text}}</van-button>
            </template>
          </van-field>
          <van-field
            v-model="newPassword"
            label="新密码"
            placeholder="6-12位由数字和字母组成的字符"
            :type="isPassword ? 'password' : 'text'"
            :right-icon="isPassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="viewPassword"
          />
          <div class="btns-box">
            <van-button class="btn" block native-type="submit" @click='checkValidCode'>
              确&nbsp;&nbsp;认
            </van-button>
          </div>

        </van-form>
      </div>
    </div>
  </div>
</template>

<script>
  import {validForm} from '../assets/js/validForm'
  import {utils} from '../assets/js/utils'
  import '../assets/less/forgot.less'
  export default {
    data(){
      return{
        phone: '',
        email: '',
        code: '',
        isSend: false,
        text: '发送验证码',
        newPassword: '',
        isPassword:true,
      }
    },
    created(){

    },
    methods: {
      back(){
        this.$router.go(-1);
      },
      // 显示密码
      viewPassword(){
        this.isPassword = !this.isPassword;
      },
      // 获取邮箱验证码
      getEmailValidCode(){
        // 验证表单
        let o = {
          email: {
            value: this.email,
            reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
            errorMsg: '邮箱格式不正确'
          }
        }

        if(!validForm.valid(o)){
          // 验证不通过
          // console.log("验证不通过")
          return;
        }

        let time = 60;
        this.isSend = true;
        let timeId = setInterval(() => {
          if(time <= 0){
            clearInterval(timeId);
            this.isSend = false;
            this.text = '重新发送'
            return;
          }
          time--;
          this.text = time + '秒后重新发送'
        }, 1000);

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
          loadingType: "spinner",
        });

        let data = utils.queryString({
          appkey: this.appkey,
          email: this.email
        })

        this.axios({
          method: 'POST',
          url:'/emailValidCode',
          data
        }).then(result => {
          this.$toast.clear();
            this.$toast(result.data.msg)
          // if(result.data.code == 'E001'){
          //   this.$toast.success(result.data.msg)
          //   this.show = false;
          //   setTimeout(() => {
          //     this.$router.push({name: 'Login'})
          //   }, 1000);
          // }else{
          //   this.$toast.fail(result.data.msg)
          // }
          // console.log("emailValidCode result ==> ",result)
        }).catch(err =>{
          this.$toast.clear()
          // console.log("emailValidCode result ==> ",err)
        })   

      },
      // 验证验证码
      checkValidCode(){

        // 验证表单
        let o = {
          phone: {
            value: this.phone,
            reg: /^1[3-9]\d{9}$/,
            errorMsg: '手机格式不正确'
          },
          email: {
            value: this.email,
            reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
            errorMsg: '邮箱格式不正确'
          },
          code: {
            value: this.code,
            reg: /^\d{6}$/,
            errorMsg: '验证码为6位数字'
          }
        }

        if(!validForm.valid(o)){
          // 验证不通过
          // console.log("验证不通过")
          return;
        }

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
          loadingType: "spinner",
        });

        let data = utils.queryString({
          appkey: this.appkey,
          validCode: this.code
        })

        this.axios({
          method: 'POST',
          url:'/checkValidCode',
          data
        }).then(result => {
          this.$toast.clear();
          this.$toast(result.data.msg)
          if(result.data.code == 'K001'){
            // this.$toast.success(result.data.msg)
            this.retrievePassword();
          }
          // console.log("checkValidCode result ==> ",result)
        }).catch(err =>{
          this.$toast.clear()
          // console.log("checkValidCode result ==> ",err)
        })   

      },      
      // 找回密码
      retrievePassword(){
        // 验证表单
        let o = {
          phone: {
            value: this.phone,
            reg: /^1[3-9]\d{9}$/,
            errorMsg: '手机格式不正确'
          },
          newPassword: {
            value: this.newPassword,
            // 6-12位由数字和字母组成的字符
            reg: /^(?![0-9]+$)(?![a-zA-Z]+$)[a-zA-z0-9]{6,12}$/,
            errorMsg: '密码格式不正确'
          }
        }

        if(!validForm.valid(o)){
          // 验证不通过
          // console.log("验证不通过")
          return;
        }

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
          loadingType: "spinner",
        });

        let data = utils.queryString({
          appkey: this.appkey,
          phone: this.phone,
          password: this.newPassword,
        })

        this.axios({
          method: 'POST',
          url:'/retrievePassword',
          data
        }).then(result => {
          this.$toast.clear();
            // this.$toast(result.data.msg)
          if(result.data.code == 'L001'){
            this.$toast.success(result.data.msg)
            setTimeout(() => {
              this.$router.push({name: 'Login'})
            }, 1000);
          }else{
            this.$toast.fail(result.data.msg)
          }
          // console.log("retrievePassword result ==> ",result)
        }).catch(err =>{
          this.$toast.clear()
          // console.log("retrievePassword result ==> ",err)
        })   

      }
    }
  }
</script>
